.voiceButton {
  width: 50px;
  height: 50px;
  /*box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);*/
  margin-bottom: 10px;
  position: relative;
  z-index: 9998;
}
.mikeButton {
  width: 60px;
  height: 60px;
  position: absolute;
  cursor: pointer;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}